
'use server';

import { getFirestore, serverTimestamp } from 'firebase-admin/firestore';
import * as admin from 'firebase-admin';
import type { ProfileData } from '@/types';
import { revalidatePath } from 'next/cache';

interface UpdateProfileRequest {
  uid: string;
  data: Partial<Omit<ProfileData, 'uid' | 'email' | 'createdAt' | 'role' | 'status' | 'subscriptionStatus'>>;
}

export async function updateUserProfile(request: UpdateProfileRequest) {
  // Directly initialize Firebase Admin SDK within the Server Action
  try {
    if (admin.apps.length === 0) {
        const serviceAccount = JSON.parse(
            process.env.FIREBASE_SERVICE_ACCOUNT_KEY as string
        );
        admin.initializeApp({
            credential: admin.credential.cert(serviceAccount),
        });
    }
  } catch (e: any) {
    console.error('Firebase Admin SDK initialization error:', e.message);
    return { success: false, message: 'Server configuration error.' };
  }

  const { uid, data } = request;
  const db = getFirestore();
  const userDocRef = db.collection('users').doc(uid);

  // Sanitize the data to prevent users from updating protected fields
  const sanitizedData: Partial<ProfileData> & { updatedAt: any } = {
    ...data,
    updatedAt: serverTimestamp(),
  };
  
  // Explicitly remove any fields a user should not be able to change
  delete (sanitizedData as any).role;
  delete (sanitizedData as any).status;
  delete (sanitizedData as any).subscriptionStatus;
  delete (sanitizedData as any).isAdmin; 
  delete (sanitizedData as any).createdAt;
  delete (sanitizedData as any).uid;

  try {
    await userDocRef.update(sanitizedData);
    revalidatePath('/profile'); // Revalidate the profile page to show new data
    return { success: true, message: 'Profile updated successfully.' };
  } catch (error: any) {
    console.error('Error updating profile:', error);
    return { success: false, message: 'Could not update profile.' };
  }
}
